import React, {FC, useEffect, useState} from "react";
import {withRouter, RouteComponentProps} from "react-router-dom";
import {useSelector, useDispatch} from "react-redux";
import {Details} from "../../pages/details/details";
import {AppState, Data} from "../../types";
import {useDetailsData} from "./useDetailsData";
import {setDetailsData, setRawData} from "../../store/actions/data/data.actions";
import {getObjAttrs} from "@nova/rui.component-lib";
import {
  setAllColumns,
  setSelectedColumns,
} from "../../store/actions/detailsTable/detailsTable.actions";

export interface Props extends RouteComponentProps<{id: string}> {
  data: Data[];
}

export const DetailsWithRouter: FC<Props> = ({data: dataProp, match}) => {
  const [detailsDataState, setDetailsDataState] = useState<Data[]>(dataProp);
  const dispatch = useDispatch();
  /*  const data = useSelector((state: AppState) => state.present.data.rawData); */
  const filters = useSelector((state: AppState) => state.present.filters.filters);
  const detailsData = useDetailsData(dataProp, filters, match.params.id);

  useEffect(() => {
    dispatch(setDetailsData(detailsData));
    setDetailsDataState(detailsData);
  }, [match.params.id]);

  useEffect(() => {
    dispatch(setSelectedColumns(getObjAttrs(detailsData[0])));
    dispatch(setAllColumns(getObjAttrs(detailsData[0])));
  }, []);

  return <Details detailsData={detailsDataState} />;
};

export const DetailsConnection = withRouter(DetailsWithRouter);
