import React, {FC} from "react";
import {GroupsTableBody} from "./components/groupsTableBody/groupsTableBody";
import {GroupsTableFooter} from "./components/groupsTableFooter/groupsTableFooter";
import {GroupsTableHeader} from "./components/groupsTableHeader/groupsTableHeader";
import {GroupedValue} from "../../types";

export interface Props {
  colors: string[];
  headerGroups: GroupedValue[];
  bodyGroups: any;
  footerGroups: GroupedValue[];
  expandedCategories: string[];
  bodyCellsView: string;
  handleExapandedCategories: (payload: string[]) => void;
}

export const GroupsTable: FC<Props> = ({
  colors,
  headerGroups,
  bodyGroups,
  footerGroups,
  expandedCategories,
  bodyCellsView,
  handleExapandedCategories,
}) => (
  <table>
    <GroupsTableHeader colors={colors} groups={headerGroups} />
    <GroupsTableBody
      colors={colors}
      groups={bodyGroups}
      expandedCategories={expandedCategories}
      handleExpandedCategories={handleExapandedCategories}
      bodyCellsView={bodyCellsView}
    />
    <GroupsTableFooter colors={colors} groups={footerGroups} />
  </table>
);
