import React, {FC, useState, useRef} from "react";
import {useSelector} from "react-redux";
import {Button, Drawer} from "antd";
import {Page, Alignment, useFilters, useSortNumbers} from "@nova/rui.component-lib";
import {
  GroupsSelectionsConnection,
  FilterConnection,
  GroupsTableConnection,
  GroupsFilterButtonsConnection,
} from "../../connections";
import {GroupsStyles} from "./groups.styles";
import {useGroups} from "../../hooks/useGroups/useGroups";
import {AppState, Data} from "../../types";

export interface Props {
  data: Data[];
}

export const Groups: FC<Props> = ({data}) => {
  const pageEl = useRef<HTMLDivElement>(null);
  const [drawerOpen, setDrawerOpen] = useState<boolean>(false);
  const [drawerNode, setDrawerNode] = useState(pageEl.current ? pageEl.current : undefined);
  /*  const data = useSelector((state: AppState) => state.present.data.rawData); */
  const filters = useSelector((state: AppState) => state.present.filters.filters);
  const sort = useSelector((state: AppState) => state.present.groupsTable.sort);
  const groupVariable = useSelector((state: AppState) => state.present.selections.groupedBy);
  const summarizedBy = useSelector((state: AppState) => state.present.selections.summarizedBy);
  const filteredData = useFilters(data, filters);
  const sortedData = useSortNumbers(filteredData, sort);
  // TODO: remove age computing from useGroups because it will be computed by getObjAttrValues in filters
  const [headerGroups, bodyGroups, footerGroups, ageRange, prevalenceRange] = useGroups(
    sortedData,
    groupVariable,
    summarizedBy,
  );
  const colors = ["blue", "red", "green", "purple", "orange", "brown", "pink", "azure"];

  const handleDrawerVisibility = () => {
    setDrawerOpen(!drawerOpen);
    setDrawerNode(pageEl.current ? pageEl.current : undefined);
  };

  const handleDrawerClose = () => {
    setDrawerOpen(false);
  };

  const getContainer = () => document.body || drawerNode;

  return (
    <div ref={pageEl} style={{position: "relative"}}>
      <Page>
        <GroupsStyles>
          <Alignment direction="row">
            <GroupsSelectionsConnection />
            <div>
              <Button icon="filter" onClick={handleDrawerVisibility}>
                Datafilter
              </Button>
            </div>
          </Alignment>
          <GroupsFilterButtonsConnection />
          <GroupsTableConnection
            colors={colors}
            headerGroups={headerGroups}
            bodyGroups={bodyGroups}
            footerGroups={footerGroups}
          />
        </GroupsStyles>
      </Page>
      <Drawer
        title="Datafilter"
        placement="left"
        closable={true}
        onClose={handleDrawerClose}
        visible={drawerOpen}
        getContainer={getContainer()}
        style={{position: "absolute", zIndex: 90}}
      >
        <FilterConnection data={data} />
      </Drawer>
    </div>
  );
};
