import React from "react";
import {useSelector} from "react-redux";
import {FilterButtons} from "@nova/rui.component-lib";

import {AppState} from "../../types";

export const DetailsFilterButtonsConnection = () => {
  const filters = useSelector((state: AppState) => state.present.filters.filters);

  return filters.length ? (
    <FilterButtons label="Groupfilters:" disabled={true} filters={filters} />
  ) : null;
};
