import React, {FC, ReactNode, useState, useRef, HTMLAttributes} from "react";
import {Icon, Dropdown, Menu} from "antd";
import {HeaderCellStyles} from "./groupsTableHeaderCell.styles";
import {SortIcon} from "./sortIcon";
import {dropdownMenu} from "./dropdownMenu";

export interface Props extends HTMLAttributes<HTMLTableCellElement> {
  groupName?: string;
  inline?: boolean;
  rowSpan?: number;
  color: string;
  column?: string;
  filters?: any;
  sort: boolean;
  filter?: "search" | "check";
  children: ReactNode | ReactNode[];
}

export const GroupsTableHeaderCell: FC<Props> = ({
  groupName,
  inline,
  rowSpan,
  column,
  filters,
  sort,
  filter,
  color,
  children,
}) => {
  const el = useRef<HTMLDivElement>(null);
  const [dropdownNode, setDropdownNode] = useState(el.current ? el.current : undefined);
  const [dropdownOpen, setDropdownOpen] = useState<boolean>(false);

  const handleVisibilityChange = (visible: boolean) => {
    setDropdownOpen(visible);
    const dropdown = el.current ? el.current : undefined;
    setDropdownNode(dropdown);
  };

  const getContainer = () => dropdownNode || document.body;

  const onFilterClick = () => null;

  const onSortClick = () => null;

  const setSortDirection = () => undefined;

  return (
    <HeaderCellStyles
      color={color}
      filterActive={false}
      dropdownVisibilty={dropdownOpen}
      rowSpan={rowSpan}
      inline={inline}
    >
      <div className="cell">
        <span className="cell-item" role="button" onClick={onSortClick}>
          {children}
        </span>
        {groupName !== "Screen Failure" ? (
          <div className="cell-handler">
            {sort && <SortIcon sortDirection={setSortDirection} />}
            {filter ? (
              <Dropdown
                overlay={<Menu />}
                trigger={["click"]}
                placement="bottomRight"
                onVisibleChange={handleVisibilityChange}
                visible={dropdownOpen}
                getPopupContainer={getContainer}
              >
                <span className="filter-btn" role="button">
                  <Icon type={filter === "search" ? "search" : "filter"} />
                </span>
              </Dropdown>
            ) : null}
          </div>
        ) : (
          <div className="empty-space" />
        )}
      </div>
      <div ref={el} className="dropdown" />
    </HeaderCellStyles>
  );
};
