import React, {FC} from "react";
import {useSelector, useDispatch} from "react-redux";
import produce from "immer";
import {Filters} from "../../components";
import {Filter, AppState, FilterValues, Data} from "../../types";
import {useFilterData} from "../../hooks/useFilterData/useFilterData";
import {setDataFilters} from "../../store/actions";

export interface Props {
  data: Data[];
}

export const FilterConnection: FC<Props> = ({data}) => {
  const dispatch = useDispatch();
  /*   const data = useSelector((state: AppState) => state.present.data.rawData); */
  const availableFilters = useSelector((state: AppState) => state.present.filters.availableFilters);
  const selectedFilters = useSelector((state: AppState) => state.present.filters.selectedFilters);
  const filters: Filter[] = useFilterData(data, availableFilters, selectedFilters);

  const handleFilterChange = (payload: FilterValues, attribute: string) => {
    const nextFilters: Filter[] = produce(filters, (draft: Filter[]) => {
      const index = draft.findIndex((d: {attribute: string}) => d.attribute === attribute);
      draft[index].selectedValues = payload;

      return draft;
    });
    dispatch(setDataFilters(nextFilters));
  };

  return <Filters filters={filters} handleFilter={handleFilterChange} />;
};
